.example-info__text {
  margin-top: 0;
  padding-right: 1px;
  margin-bottom: 27px;
  font-family: $font-text;
}

.example-info__list {
  @include reset-list();
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 9px;
}

.example-info__item {
  width: calc(50% - 16px);
  text-align: center;
}

.example-info__value {
  margin: 0;
  padding: 14px 20px 15px 20px;
  font-size: 24px;
  line-height: 24px;
  color: $dark;
  text-transform: uppercase;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
}

.example-info__desc {
  margin: 0 18px;
  display: inline-block;
  transform: translateY(-9px);
  padding: 0 15px;
  font-size: 12px;
  line-height: 12px;
  font-family: $font-text;
  background-color: $grey;
}

.example-info__costs {
  margin: 0;
  margin-bottom: 34px;
  line-height: 20px;
  text-align: center;
  color: $dark;
  text-transform: uppercase;
}

@media (min-width: $tablet-width) {
  .example-info {
    margin-bottom: 59px;
  }

  .example-info__text {
    font-size: 16px;
    line-height: 24px;
    padding-right: 30px;
    margin-bottom: 54px;
  }

  .example-info__stats {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .example-info__list {
    width: calc(50% + 12px);
  }

  .example-info__item {
    width: calc(50% - 21px);
  }

  .example-info__value {
    font-size: 30px;
    line-height: 37px;
    padding-top: 13px;
    padding-bottom: 20px;
  }

  .example-info__desc {
    margin-left: 7px;
    margin-right: 7px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 14px;
    line-height: 14px;
    background-color: #ffffff;
    transform: translateY(-12px);
  }

  .example-info__costs {
    width: calc(50% - 12px);
    padding-top: 10px;
    text-align: right;
    font-size: 20px;
    line-height: 20px;
  }

  .example-info__sum {
    display: block;
    margin-top: 10px;
  }
}

@media (min-width: $desktop-width) {
  .example-info {
    width: 465px;
    margin: 0;
    padding: 0;
    padding-top: 71px;
  }

  .example-info__text {
    margin-bottom: 68px;
  }

  .example-info__stats {
    flex-direction: column;
    width: 366px;
  }

  .example-info__list {
    width: auto;
    margin-bottom: 42px;
  }

  .example-info__desc {
    background-color: $grey;
  }

  .example-info__costs {
    width: 100%;
    text-align: left;
    padding: 0;
  }

  .example-info__sum {
    display: inline-block;
    margin-top: 0;
    margin-left: 59px;
  }
}
